.nav {
    color: white;
// background-color: var(--main);
position: fixed;
top: 0;
width: 100%; 
z-index: 999;
// background:linear-gradient(to top,transparent 0% rgb(0, 0, 0, 0.3) 50%);
&.scrolled{
background-color: var(--main);
}
.container{
padding: 0px 50px;
display: flex;
align-items: center;
justify-content: space-between;
height: 70px;
background-color: grey;
}
.left{
    display: flex;
    align-items: center;
    img {
        padding-top: 5px;
        height: 35px;
        width: 120px;
        border-radius: 10px;
        margin-right: 40px;
    }
 
    span{
        margin-right: 20px;
        cursor: pointer;
    }
}
.right{
    display:flex ;
    align-items: center;
    img{
        height: 30px;
        width: 30px;
        border-radius: 15px;
        object-fit: cover;
        cursor: pointer;
    }

    .profile{
        .options{
          display: none;
          background-color: var(--main);
          border-radius: 5px;
        }
        span{
            padding: 10px;
            cursor: pointer;
        }
        &:hover{
            .options{
                display: flex;
                flex-direction: column;
                position: absolute;
            }
        }
    }

    .icons{
        margin: 0px 15px;
        cursor: pointer;
    }

}


}