.list {
    width: 100%;
    margin-top: 10px;

    .listTitle {
        color: white;
        font-size: 20px;
        font-weight: 500;
        margin-left: 10px;
    }

    .wrapper {
        position: relative;

        .sliderarrow {
            width: 50px;
            height: 100%;
            background-color: rgb(22, 22, 22);
            opacity: 0.5;
            color: white;
            position: absolute;
            z-index: 999;
            top: 0;
            bottom: 0;
            margin: auto;
            cursor: pointer;

            &.left {
                left: 0;
            }

            &.right {
                right: 0;
            }

        }

        .container {
            display: flex;
            margin-left: 20px;
            width: max-content;
            margin: 10px;
            transform: translateX(0px);
             transition: all 1s ease;
        }
    }
}