.register{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
    rgba(0,0,0,0) 0% ,
    rgba(0,0,0,1) 100%),url("https://cinecelluloid.com/wp-content/uploads/2016/10/Netflix-India.jpg");
    // background-size: cover;
   position: relative;
   
    .top{
    .wrapper{
         display: flex;
         padding: 8px 20px;
         align-items: center;
         justify-content: space-between;

        .logo{
            margin-left: 0px;
            height: 50px;
            width: 150px;
           
        }
        .loginbtn{
            background-color: red;
            border: none;
            color: white;
            border-radius: 5px;
            padding: 5px 15px;
            font-size: 20px;
            font-weight: 500;
            cursor: pointer;
        }
    }
}
.container{
   width: 100%;
   height: 100%;
   position: absolute;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   color: white;
  
   h1{
    font-size:60px;
   }
   h3{
    margin: 20px;
    font-size: 30px;
   }
   p{
    font-size: 20px;
   }
   .input{
    width: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border: none;
    height: 50px;
    border-radius: 5px;
    input{
        flex: 9;
        height: 100%;
        border: none;
        padding: 0 10px;
    }
    .registorbtn{
        flex: 3;
        height: 100%;
        background-color: red;
        color: white;
        font-size: 22px;
        cursor: pointer;
        border: none;
    }
   }

}
}