.login{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
    rgba(0,0,0,0) 0% ,
    rgba(0,0,0,1) 100%),url("https://cinecelluloid.com/wp-content/uploads/2016/10/Netflix-India.jpg");
    // background-size: cover;
      position: relative;
   
    .top{
    .wrapper{
         display: flex;
         padding: 8px 20px;
         align-items: center;
         justify-content: space-between;

        .logo{
            margin-left: 0px;
            height: 50px;
            width: 150px;
           
        }
       
    }
}
.container{
   width: 100%;
   height: 100%;
   position: absolute;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   color: white;
    form{
        width: 500px;
        height: 500px;
        padding: 30px;
        border-radius: 5px;
        background-color: var(--main);
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        opacity: 1.3;
        border-radius: 30px;
        input{
            height: 40px;
            width: 100%;
            background-color: grey;
            &::placeholder{
             color: lightgrey;
            }
            border-radius: 8px;
            text-align: center;
        }
        button{
            width: 100%;
            height: 40px;
            border-radius: 10px;
            background-color: red;
            color: white;
            font-size: 20px;
            font-weight: 600;
            border: none;
            cursor: pointer;
        }
    }
}
}